import { Chart } from 'chart.js/auto'


const renderChart = async () => {
  const data = [
    { cpu: 2010, time: 30 },
    { cpu: 2011, time: 20 },
    { cpu: 2012, time: 15 },
    { cpu: 2013, time: 25 },
    { cpu: 2014, time: 22 },
    { cpu: 2015, time: 30 },
    { cpu: 2016, time: 28 },
  ];

  new Chart(
    document.getElementById('acquisitions'),
    {
      type: 'polarArea',
      data: {
        labels: data.map(row => row.cpu),
        datasets: [
          {
            label: 'Acquisitions by year',
            data: data.map(row => row.time)
          }
        ]
      }
    }
  );
};

export default renderChart;